













































































import App from '../../../../App.vue'
import Api from '../../../../api.js'
export default {
	data() {
		return {
			logined:true,
			nologined:false,
			showed:false,
			url: '',
			showed1: false,
			showed2: false,
			avatarUrl: '',
			name: '',
			shopName: '',
			shops: [],
			commodity:[
		    {
				type:'单月套餐',
				src:'../../../../static/image/month-01.png',//单月
			},
			{
				type:'季度套餐',
				src:'../../../../static/image/quarter-01.png',//季度
			},
			{
				type:'半年套餐',
				src:'../../../../static/image/halfyear-01.png',//半年
			},
			{
				type:'年度套餐',
				src:'../../../../static/image/year-01.png',//年度
			}
			]
		};
	},
	methods: {
		//登录
		login:function(){
				uni.navigateTo({
					url: '../login'
				})
		},
		//个人信息编辑
		edit:function(){
			if(App.getToken()){
				uni.navigateTo({
					url:'./edit/edit?avatarUrl='+this.avatarUrl+'&'+'name='+this.name
				})
			}
			else{
				uni.showToast({
					title:'您尚未登录'
				})
			}
			},
		show:function(){
			this.showed=!this.showed
		},
		//错误图片
		errorImage: function() {
			this.avatarUrl = this.url
		},
		//picker改变
		bindPickerChange: function(e) {
			var that = this
			console.log(e)
			console.log('picker发送选择改变，携带值为', e.target.value)
			var ind = e.target.value
			that.shopName = that.shops[e.target.value].company_name
			App.savecompany(that.shops[e.target.value])
		},
		//跳转（个人订单、钱包、门店管理）
		jump(url) {
			if (!url) return;
			uni.navigateTo({
				url
			});
		},
		//产看商城
		scan(index){
			uni.navigateTo({
				url:'./commodity-detail/commodity-detail?index='+index
			});
		},
		//二维码（本页面没有）
		
	},
	onShow() {
		var that = this;
		if (App.getToken()) {
			console.log(App.getcompany())
			//内存中有公司
			if (App.getcompany()) {
				that.showed1 = false//显示机构，否则显示游客
				that.showed2 = true
				this.shopName = App.getcompany().company_name;
				uni.request({
					url: Api.shop(),
					header: {
						token: App.getToken()
					},
					success: function(res) {
						if (res.data.code == 200) {
							that.shops = res.data.value
						} else {
							uni.showToast({
								title: '获取门店失败',
								duration: 2000,
								icon: 'none'
							})
						}
					},
				})
			} else {
				uni.request({
					url: Api.shop(),
					header: {
						token: App.getToken()
					},
					success: function(res) {
						if (res.data.code == 200) {
							that.showed1 = false
							that.showed2 = true
							that.shopName = '请选择门店'
							console.log(res)
							that.shops = res.data.value;
							console.log(that.shops);
						} else if (res.data.code == 1005) {
							that.showed1 = true
							that.showed2 = false
							// uni.showToast({
							// 	title: '您不属于任何商家',
							// 	duration: 2000,
							// 	icon:'none'
							// })
						}
					}
				})
			}
			console.log(App.getcompany().company_name);
			uni.request({
				url: Api.seeUser(),//查看个人信息
				header: {
					token: App.getToken(),
				},
				data: {
					otherId: App.getUserId()
				},
				success(res) {
					console.log(res)
					if (res.data.code) {
						uni.showToast({
							title: 'token失效！',
							icon: 'none'
						})
						uni.clearStorage()
					} else {
						that.url = App.geturlerror(res.data.user.author_image)
						console.log(that.url)
						that.avatarUrl = res.data.user.author_image
						that.name = res.data.user.author_name
					}
				}
			})
		} else {
			this.name='点击授权登录'
			this.logined=false
			this.nologined=true
			this.url='../../../../static/img/tabbar/me.png'
		}
	}
}
